import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom'
import categories from '../data/categories'
import { subChange } from './../store/subSlice';

function SubNav() {
  const subHidden = useSelector((state) => state.subHidden)
  const dispatch = useDispatch()
  
  return (
    <div className={subHidden}>
      <div className='nav-container'>
        <header>
          <h2>!</h2>
          <a> </a>
        </header>
        <nav>
          <h2></h2>
          <ul>
            <Link to='/goods/list/0'>
              <li onClick={() => dispatch(subChange()) }></li>
            </Link>
            <Link to='/goods/detail/6'>
              <li onClick={() => dispatch(subChange()) }>   </li>
            </Link>
            <Link to='/goods/detail/12'>
              <li onClick={() => dispatch(subChange()) }>  </li>
            </Link>
            <Link to='/goods/detail/3'>
              <li onClick={() => dispatch(subChange()) }>[]   </li>
            </Link>
          </ul>
          <h2><span></span></h2>
          <ul>
            { categories.map((data) => {
              let link = '/goods/list/' + data.id
              return (
                <Link to={link} key={data.id}>
                  <li onClick={() => dispatch(subChange()) }>{data.category}</li>
                </Link>
              )
            })}
          </ul>
        </nav>
        <footer>
          <div>
            <h2></h2>
            <div>1:1 </div>
            <div>|</div>
            <div> </div>
          </div>
        </footer>
      </div>
      <div className='nav-bg'/>
      <div onClick={() => { dispatch(subChange()) }} className='close-btn'></div>
    </div>
  )
}

export default SubNav